import map from 'lodash/map'
import { commonBarLabel } from './newCarDealer';

export interface ActiveAlert {
  active_rate: number
  name: string
  valid_dealer: number
}

export type ActiveAlertData = ActiveAlert[]

/**
 * EChartslabel
 * @param color (formattercallback)
 */
export function formatterCircle (color: string) {
  return `<span style="display:inline-block;margin-right:5px;border-radius:10px;width:9px;height:9px;background-color: ${color}"></span>`
}

// label
export const normalPercentTooltip = {
  tooltip: {
    trigger: 'item',
    formatter: ({ name, value, seriesName, color }) => `${seriesName}<br/>${formatterCircle(color)}${name} : ${value}%`
  },
}

/**
 * 
 * @param data 
 * @param fontSize 
 */
export const activeAlertOptions = (
  data: ActiveAlertData,
  fontSize: number
) => {
  const [names, rates, dealers] = [
    map(data, 'name'),
    map(data, 'active_rate'),
    map(data, 'valid_dealer')
  ]

  return (
    {
      color: ['#73f5fd'],
      tooltip: {
        trigger: 'item'
      },
      grid: [{
        left: '20%',
        bottom: '15%',
        top: '30%',
      }],
      xAxis: [
        {
          type: 'value',
          position: 'left',
          axisTick: {
            lineStyle: {
              color: 'transparent',
            }
          },
          axisLabel: {
            textStyle: {
              fontSize
            }
          },
          splitLine: {
            lineStyle: {
              color: 'rgba(255, 255, 255, .2)'
            },
            show: true
          }
        },
        {
          type: 'value',
          position: 'top',
          axisTick: {
            lineStyle: {
              color: 'transparent',
            }
          },
          axisLabel: {
            show: true,
            formatter: '{value}%',
            textStyle: {
              fontSize,
            }
          },
        }
      ],
      yAxis: {
        type: 'category',
        data: names,
        axisTick: {
          lineStyle: {
            color: 'transparent',
          }
        },
        axisLine: {
          lineStyle: {
            color: "rgba(255, 255, 255, .8)"
          }
        },
        axisLabel: {
          width: 70,
          overflow: 'truncate',
          textStyle: {
            fontSize,
          }
        },
      },
      legend: {
        data: [
          { name: '', icon: 'rect' },
          { name: '' },
        ],
        textStyle: {
          fontSize,
          color: '#fff',
        },
        top: '5%'
      },
      series: [
        {
          ...commonBarLabel('right'),
          data: dealers,
          name: '',
          type: 'bar',
          barWidth: 14,
          xAxisIndex: 0,
          showBackground: true,
          backgroundStyle: {
            color: 'rgba(12, 249, 255, 0)'
          },
        },
        {
          ...normalPercentTooltip,
          name: '',
          type: 'line',
          symbolSize: 0,
          color: ['#ec6660'],
          xAxisIndex: 1,
          lineStyle: {
            color: '#ec6660'
          },
          data: rates,
          label: {
            show: true,
            position: 'top',
            formatter: params => params.value === 0 ? `${params.value}%` : `${params.value.toFixed(1)}%`,
            textStyle: {
              fontSize,
              align: 'center',
              color: '#ff5a5a',
            },
          },
        }
      ]
    }
  )
}
